import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ExpenseAddComponent } from './expense-add.component';
import { ExpenseServiceService } from 'src/app/core/services/expense-service.service';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { TitleCasePipe } from '@angular/common';
import * as moment from 'moment';

@Component({
  selector: 'app-expense-edit',
  templateUrl: './expense-edit.component.html',
  styleUrls: ['./expense-edit.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ExpenseEditComponent implements OnInit {
  public expenseId: any = ""
  public expenseForm: any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public todaysDate: any;
  public expenseCategoryData: any;
  public fieldArray: Array<any> = [];
  public newAttribute: any = {};
  public formArrayData;
  public submitted: boolean = false;
  public expenseitems = []
  public expenseitemslength: any;
  public toatalAmount: any;
  today: Date;
  public deleteArray: any = [];

  constructor(
    private cdr: ChangeDetectorRef, @Inject(MAT_DIALOG_DATA) public expenseData: any, private dialogRef: MatDialogRef<ExpenseEditComponent>, private expenseservice: ExpenseServiceService,
    private formBuilder: FormBuilder, public router: Router, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe,
  ) { }

  ngOnInit(): void {
    this.today = new Date();
    this.expenseId = this.expenseData.expenseId;
    this.getExpenseById();
    this.getExpensecategory();
    this.expenseForm = this.formBuilder.group({
      expenseCategory: ["", [Validators.required]],
      expenseDate: ["", [Validators.required]],
      paymentMode: [null],
      expenseMode: [null],
      toatalAmount: ["", [Validators.required]],
      expenseNote: [null],
      addtionalDetails: new FormArray([]),

    });
    this.additionalDetailsControls.valueChanges.subscribe(data => {
      this.toatalAmount = data.reduce((a, b) => a + +b.totalAmount, 0)
      this.expenseForm.patchValue({
        toatalAmount: this.toatalAmount
      })
    })

  }

  //set value of total = price per unit * quantity
  setTotalValue(i) {
    let cur_obj = this.additionalDetailsControls.controls[i].value
    cur_obj.totalAmount = cur_obj.quantity * cur_obj.pricePerUnit
    cur_obj.totalAmount = cur_obj.totalAmount.toString();
    this.additionalDetailsControls.controls[i].patchValue(cur_obj)

  }

  get additionalDetailsControls() {
    return this.expenseForm.get('addtionalDetails') as FormArray;
  }


  deleteaddAddtionalDetails(index, i): void {
    let deletedata = index.value.expenseitemsId
    const data = this.expenseForm.get('addtionalDetails') as FormArray;
    this.deleteArray.push(deletedata);
    data.removeAt(i);


  }


  createaddtionalDetails(): FormGroup {
    return new FormGroup({
      expenseitemsId: new FormControl(),
      item: new FormControl(""),
      quantity: new FormControl(""),
      pricePerUnit: new FormControl(""),
      totalAmount: new FormControl(""),
    });
  }
  addAddtionalDetails(): void {
    const data = this.expenseForm.get('addtionalDetails') as FormArray;
    data.push(this.createaddtionalDetails());
    this.cdr.detectChanges();
  }

  get f() {
    return this.expenseForm.controls;
  }
  cancel() {
    this.dialogRef.close();
  }

  getExpensecategory() {
    this.expenseservice.getExpenseCategory({}).then((res: any) => {
      this.expenseCategoryData = res.data.rows;
    })

  }
  SetExpenseNAture(e) {
    const CategortType = this.expenseCategoryData.filter((res) => res.id === e).map(item => item.type);
    this.expenseForm.patchValue({
      expenseMode: CategortType[0]
    })
  }

  getExpenseById() {
    this.expenseservice.GetExpenseBYId({}, this.expenseId).then((res: any) => {


      this.expenseForm.patchValue({
        expenseCategory: res.data.expensecategory.id,
        expenseDate: res.data.date ? (moment(res.data.date).format("DD-MMM-YYYY")) : (moment(res.data.date).format("DD-MMM-YYYY")),
        paymentMode: res.data.paymentMode,
        expenseMode: res.data.expenseMode,
        toatalAmount: res.data.totalAmount,
        expenseNote: res.data.note
      })
      this.expenseitems = res.data.expenseitems;

      this.expenseitemslength = this.expenseitems.length;
      if (this.expenseitemslength > 0) {
        for (let i = 0; i < this.expenseitemslength; i++) {
          if (this.expenseitems[i].id) {
            this.additionalDetailsControls.push(
              new FormGroup({
                item: new FormControl(this.expenseitems[i].item.toString()),
                quantity: new FormControl(this.expenseitems[i].quantity.toString()),
                pricePerUnit: new FormControl(this.expenseitems[i].pricePerUnit.toString()),
                totalAmount: new FormControl(this.expenseitems[i].totalAmount.toString()),
                expenseitemsId: new FormControl(this.expenseitems[i].id),
              })
            )


          }
          else {
            this.additionalDetailsControls.push(
              new FormGroup({
                item: new FormControl(this.expenseitems[i].item.toString()),
                quantity: new FormControl(this.expenseitems[i].quantity.toString()),
                pricePerUnit: new FormControl(this.expenseitems[i].pricePerUnit.toString()),
                totalAmount: new FormControl(this.expenseitems[i].totalAmount.toString()),
              })
            )


          }

        }

      }
    })
  }
  ExpenseEditDetails() {
    this.submitted = true;
    this.formArrayData = this.additionalDetailsControls.value;

    if (this.expenseForm.invalid) {
      this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else {

      let body =
      {
        "userId": this.currentUser.id,
        "expenseData": {
          "date": moment(this.expenseForm.controls.expenseDate.value, 'DD-MMM-YYYY').format('YYYY-MM-DD'),
          "expensecategoryId": this.expenseForm.controls.expenseCategory.value,
          "paymentMode": this.expenseForm.controls.paymentMode.value,
          "expenseMode": this.expenseForm.controls.expenseMode.value,
          "totalAmount": this.expenseForm.controls.toatalAmount.value,
          "note": this.expenseForm.controls.expenseNote.value,
        },
        "expenseItems": this.formArrayData,
        "expenseDelete": this.deleteArray
      }
      this.expenseservice.ExpneseUpdate(body, this.expenseId).then((res) => {
        this.toastService.openSnackBar("Expense Updated Successfully!!!");

        this.router.navigate(["/pages/expense_page"]);
        this.expenseservice.notifyPartyAdded()
        this.dialogRef.close();
      }
        , (err) => {
          if (err.error.expose) {
            this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
          }
          else {
            this.toastService.openErrorSnackBar("Something Went Wrong.");
          }
        }
      )

    }
  }


}
