import { TitleCasePipe, UpperCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { ExpenseServiceService } from 'src/app/core/services/expense-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { ExpenseAddComponent } from './expense-add.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ExpenseViewComponent } from './expense-view.component';
import { ExpenseEditComponent } from './expense-edit.component';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import * as moment from 'moment';
import * as XLSX from 'xlsx';
interface IRange {
  value: Date[];
  label: string;

}

@Component({
  selector: 'app-expense-list',
  templateUrl: './expense-list.component.html',
  styleUrls: ['./expense-list.component.scss']
})
export class ExpenseListComponent implements OnInit {
  public expenseList: any[]=[];
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public currentPageNo: number = 0;
  public partyPageSize: number = 5;
  public p = 1;
  public totalrow: any;
  public toatlPages: any;
  public pagesize: number = 1;
  public expenseId: any;
  public searchKey: any = "";
  public form: FormGroup;


 
  public checkSingleArray: any = [];
  public isChecked: boolean = false;
  public upiCheckedId = [];
  public allSelect: boolean = false;
  public checkSelect: boolean = false;
  public checkSelectsingle:boolean =false;
  public ranges: IRange[];
  public dateRange:Date[];
  public today: Date;
  fileName= 'expense.xlsx';
  public selectType=['DIRECT','INDIRECT']
  constructor(private expenseservice: ExpenseServiceService, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, private dialog: MatDialog,private formBuilder: FormBuilder,) {
      this.today = new Date();
     }

  ngOnInit(): void {
    this.expenseservice.partyAdded$.subscribe(() => {
      // Fetch the updated list of users
      this.getAllExpense();
    });
    this.getAllExpense();
    this.form = this.formBuilder.group({
      checkArray: this.formBuilder.array([]),
    });

   // this.dateRange=[new Date(new Date().setDate(new Date().getDate() - 30)), new Date(new Date().setDate(new Date().getDate()))]

    this.ranges = [
       
      {
        value: [new Date(new Date().setDate(new Date().getDate() - 7)), new Date(new Date().setDate(new Date().getDate() - 1))],
        label: 'Last 7 Days'
      },
      {
        value: [new Date(new Date().setDate(new Date().getDate() - 30)), new Date(new Date().setDate(new Date().getDate() - 1))],
        label: 'Last 30 Days'
      },
      {
        value: [new Date(new Date(new Date(new Date().setMonth(new Date().getMonth() - 1)).setDate(1)).toDateString()), new Date(new Date(new Date().setDate(0)).toDateString())],
        label: 'Last Month'
  
      },
      {
        value: [new Date(new Date(new Date().getFullYear() - 1, 3, 1).toDateString()), new Date(new Date(new Date().getFullYear(), 2, 31).toDateString())],
        label: 'Last Financial Year'
  
      },
      {
        value: [new Date(new Date(new Date().getFullYear() , 3, 1).toDateString()), new Date(new Date().setDate(new Date().getDate() - 1))],
        label: 'This Financial Year'
  
      },
      {
        value: [],
        label: 'Custom Range'
  
      }];
  }
  getAllExpense() {
   this.expenseservice.getAllExpenseDetails({}, this.currentUser.id, this.partyPageSize, (this.currentPageNo+1)).then((res) => {
      console.log("res", res);
      this.p = res.data.currentPage;
      this.expenseList = res.data.pageData;
      this.totalrow = res.data.totalrows;
      this.toatlPages = res.data.totalPages

    }
      , (err) => {
        if (err.error.expose) {
          this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.openErrorSnackBar("Something Went Wrong.");
        }

      })

  }
  handlePage(e) {
    console.log(e)
    this.currentPageNo = e.pageIndex;
    this.pagesize = e.pageSize;
    if (this.expenseList.length > 0) {
      this.partyPageSize = e.pageSize;
      this.getAllExpense();
    }
  }

  

  getExpenseId(expenseId) {
    this.expenseId = expenseId

  }
  deletExpenseById() {
    let request = {
      "deleteArray": this.expenseId

    }
    this.expenseservice.deleteEpenseSingle(request, this.currentUser.id).then((res) => {
     // this.expenseList = res.data.pageData;
      // this.p = res.data.currentpage;
      // //this.expenseList = res.data.pageData;
      // this.totalrow = res.data.totalrows;
      // this.toatlPages = res.data.totalPages;
      this.toastService.openSnackBar("Expnese Deleted Successfully!!!");
      this.getAllExpense()
    
      this.checkSelectsingle = false;
      this.upiCheckedId = [];
      this.checkSelect=false;
      this.allSelect=false;
     
    }
      , (err) => {
        if (err.error.expose) {
          this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.openErrorSnackBar("Something Went Wrong.");
        }
      })
  }
  getSearchByExpenseNature(searchKey) {
    if (searchKey) {
      this.expenseservice.SearchExpense({}, this.currentUser.id, this.partyPageSize, (this.currentPageNo+1), searchKey).then((res: any) => {
        this.p = res.data.currentpage;
        this.expenseList = res.data.pageData;
        this.totalrow = res.data.totalrows;
        this.toatlPages = res.data.totalPages;
      } , (err) => {
              if (err.error.expose) {
                this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
              }
              else {
                this.toastService.openErrorSnackBar("Something Went Wrong.");
              }
            })
    }
  }
  openDialog(): void {
    const dialogRef: MatDialogRef<ExpenseAddComponent> = this.dialog.open(ExpenseAddComponent, {
      width: 'auto',
      height: '700px',
      data: {},
      panelClass: 'custom-dialog-expense-class',
      disableClose:true,
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }
  openViewModal(expenseId: string): void {
    const dialogRef = this.dialog.open(ExpenseViewComponent, {
      width: '1000px', // Adjust the width as per your requirement
      height: '600px', // Adjust the height as per your requirement
      data: { expenseId: expenseId },
      panelClass: 'custom-dialog-expense-class',
   //   disableClose:true,
    });

    dialogRef.afterClosed().subscribe(result => {

    });
  }
  openEditModel(expenseId: string) {

    const dialogRef = this.dialog.open(ExpenseEditComponent, {
      width: '1000px', // Adjust the width as per your requirement
      height: '600px', // Adjust the height as per your requirement
      data: { expenseId: expenseId },
      panelClass: 'custom-dialog-expense-class',
      disableClose:true,
    });

    dialogRef.afterClosed().subscribe(result => {

    });

  }
  onChekedsingleCheck(e){
    const checkArray: FormArray = this.form.get('checkArray') as FormArray;
    if (e.target.checked) {
      this.checkSingleArray.push(e.target.value);
    }
    else {
      this.checkSingleArray = [];
    }
  }

  
  getSingleCheckedId(expenseId){
    this.expenseId = expenseId;

  }
  onChekMultiple($event){
    let id = $event.target.value;
    this.isChecked = $event.target.checked;
    this.upiCheckedId = this.expenseList.map((item) => {
      if (id == -1) {
        if (this.isChecked == true) {
          this.checkSelectsingle = true;
          return item.id;
        }
        else {
          this.checkSelectsingle = false;
          this.upiCheckedId = [];
        }
      }
    })

  }
  getUPIcheckId(expenseId){
    this.expenseId = expenseId;

  }
  changeDate(_e: any){
    this.getExcelData();  
    }

    getExcelData(){
    this.expenseservice.downloadExcel({}, this.currentUser.id,moment(this.dateRange[0], 'DD-MMM-YYYY').format('YYYY-MM-DD') ,moment(this.dateRange[1], 'DD-MMM-YYYY').format('YYYY-MM-DD')).then((res:any) => {
      console.log("res", res);
      this.p = res.data.currentPage;
      this.expenseList = res.data.pageData;
      this.totalrow = res.data.totalrows;
      this.toatlPages = res.data.totalPagess

    }
      , (err) => {
        if (err.error.expose) {
          this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.openErrorSnackBar("Something Went Wrong.");
        }

      })

  }


    ExcelDownlod(){
          /* pass here the table id */
    let element = document.getElementById('excel-table');
    const ws: XLSX.WorkSheet =XLSX.utils.table_to_sheet(element);
 
    /* generate workbook and add the worksheet */
    const wb: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');
 
    /* save to file */  
    XLSX.writeFile(wb, this.fileName);

    }

}
